.header {
  width: 100%;
  height: 5rem;
  position:fixed;
  z-index: 1;
  top:0;
  display: flex;
  padding: 0 10%;
  justify-content: space-between;
  align-items: center;
  background-color: #008080;
}

.logo {
  font-size: 2rem;
  color: white;
}

.nav ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.nav li {
  margin-left: 1.5rem;
  font-size: 1.25rem;
}

.nav a {
  text-decoration: none;
  color: #88dfdf;
}

.nav a:hover,
.nav a:active,
.nav a.active {
  color: #e6fcfc;
}

@media screen and (max-width: 760px)  {
  .logo {
    font-size: 1rem;
    color: white;
  }

  .nav li {
    margin-left: 1rem;
    font-size: .5rem;
  }
}